export default {
	data() {
		return {
			UserInfo: {}
		}
	},
	mounted() {
		this.getUserInfoMt()
	},
	methods: {
		getUserInfoMt() {













			
			// 查看是否授权
			wx.getSetting({
			  success (res){
				if (res.authSetting['scope.userInfo']) {
				  // 已经授权，可以直接调用 getUserInfo 获取头像昵称
				  wx.getUserInfo({
					success: function(res) {
						this.UserInfo = res.userInfo
					  console.log(res.userInfo)
					}
				  })
				}
			  }
			})
		},
	}
}
